@font-face{
  font-family: Montserrat;
  src: url(assets/Montserrat-Bold.ttf);
  font-weight: 800;
}

@font-face{
  font-family: Roboto;
  src: url("https://fonts.googleapis.com/css?family=Noto+Sans+JP&display=swap");
  font-weight: 800;

}


body{
  text-align: center;
  font-size: 20px;
  font-family: 'Roboto';
  
}


h1{
  font-family: 'Montserrat';
  font-size: 100px;
  text-align: center;
  color: white;
  width: 100%;
  height: 150px;
  vertical-align: center;
  margin-top: 0;
  margin-bottom: 0;
  
  background-image: url(law.jpg);
}
h2, h4, h5 {
  font-family: 'Montserrat';
  font-size: 40px;
  text-align: center;
  width: 100%;
  height: 20px;
  vertical-align: center;
  margin-top: 0;
  margin-bottom: 0;
}

h3 {
  font-family: 'Montserrat';
  font-size: 20px;
  text-align: center;
  width: 100%;
  height: 20px;
  vertical-align: center;
  margin-top: 0;
  margin-bottom: 0;
}


.profile{
  width: 200px;
  height: 200px;
  border-radius: 100px;
}

figure {
  display: inline-block;
  margin: 20px; /* adjust as needed */
}
figure img {
  vertical-align: top;
}
figure figcaption {
  text-align: center;
}

.center-div
{
  margin-left: auto;
  margin-right: auto;
}

